  <el-table-column label=\"团队人员个数\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       @click=\"detailsVisible=true,details(scope.row)\">{{scope.row.number}}</el-button>\n          </template>\n        </el-table-column>\n\n      </el-table>\n      <el-dialog title=\"详情\"\n                 center\n                 :visible.sync=\"detailsVisible\">\n        <div class=\"details\"\n             v-if='leaderDetails'>\n          <div>\n            <span>姓名 : </span>\n            <span>{{leaderDetails.name}} </span>\n          </div>\n          <div>\n            <span>手机号 : </span>\n            <span>{{leaderDetails.phone}} </span>\n          </div>\n          <div>\n            <span>服务地区 : </span>\n            <span>{{leaderDetails.city}} </span>\n          </div>\n          <div>\n            <span>成员 : </span>\n\n          </div>\n\n        </div>\n        <el-table :data=\"detailsList\"\n                  border\n                  class=\"table\">\n          <el-table-column align=\"center\"\n                           label=\"姓名\"\n                           prop=\"name\">\n\n          </el-table-column>\n          <el-table-column align=\"center\"\n                           label=\"收益(元)\"\n                           prop=\"total\">\n\n          </el-table-column>\n        </el-table>\n      </el-dialog>\n      <!-- 分页 -->\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      //分页数据\n      token: window.sessionStorage.getItem('bbytoken'),\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      authList: [],\n      page: 1,\n      pageCount: 0,\n      list: [],\n      detailsVisible: false,\n      detailsList: [],\n      leaderDetails: {}\n    }\n  },\n  methods: {\n    paging (e) {\n      this.page = e\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('Admin/VipManager/teamList', { token: this.token, page: this.page })\n        this.list = res.data.data.data || []\n        this.pageCount = res.data.data.rows || 0\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //团队详情\n    async details (row) {\n      try {\n        const res = await this.$axios.post('admin/VipManager/teamDetail', { token: this.token, id: row.id })\n        this.leaderDetails = res.data.data.leader\n        this.detailsList = res.data.data.list\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //通过审核\n    async through () {\n      try {\n        const res = await this.$axios.post('admin/ShopAudit/passShop', { token: this.token, sid: this.sid, aid: this.aid })\n        if (res.data.code == 1) {\n          this.detailsVisible = false\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '团队列表') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n              }\n\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n\n  },\n  mounted () {\n    this.init()\n    this.erAuth()\n  },\n\n\n}\n</script>\n<style scoped>\n.details {\n  margin-bottom: 20px;\n}\n.details > div {\n  padding: 5px 0;\n}\n>>> .el-table th {\n  padding: 0px 0 !important;\n}\n.details > div > span:first-child {\n  display: inline-block;\n  width: 65px;\n  text-align: right;\n  margin-right: 10px;\n}\n</style>"],"sourceRoot":""}]);

